<template>
  <div id ="portfolio">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="">
        <el-input v-model="form.name"></el-input>
      </el-form-item>
      <el-form-item label="">
        <el-select v-model="form.region" placeholder="">
          <el-option label="Angular" value="Angular"></el-option>
          <el-option label="React" value="React"></el-option>
          <el-option label="Vue" value="Vue"></el-option>
        </el-select>
      </el-form-item>
      <el-form-item label="">
        <el-col :span="11">
          <el-date-picker type="date" placeholder="" v-model="form.date1" style="width: 100%;"></el-date-picker>
        </el-col>
        <el-col class="line" :span="2">-</el-col>
        <el-col :span="11">
          <el-time-picker type="fixed-time" placeholder="" v-model="form.date2" style="width: 100%;"></el-time-picker>
        </el-col>
      </el-form-item>
      <el-form-item label="">
        <el-switch on-text="" off-text="" v-model="form.delivery"></el-switch>
      </el-form-item>
      <el-form-item label="">
        <el-checkbox-group v-model="form.type">
          <el-checkbox label="" name="type"></el-checkbox>
          <el-checkbox label="" name="type"></el-checkbox>
          <el-checkbox label="" name="type"></el-checkbox>
          <el-checkbox label="" name="type"></el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="">
        <el-radio-group v-model="form.resource">
          <el-radio label=""></el-radio>
          <el-radio label=""></el-radio>
        </el-radio-group>
      </el-form-item>
      <el-form-item label="">
        <el-input type="textarea" v-model="form.desc"></el-input>
      </el-form-item>
      <el-form-item>
        <el-popover
          ref="popover"
          placement="top"
          width="160"
          v-model="visible">
          <p></p>
          <div style="text-align: right; margin: 0">
            <el-button size="mini" type="text" @click="visible = false"></el-button>
            <el-button type="primary" size="mini" @click="visible = false"></el-button>
          </div>
        </el-popover>
        <el-button type="primary" v-popover:popover></el-button>
        <el-button></el-button>
      </el-form-item>
    </el-form>
    <a href="http://www.kulolo.top/portfolio" target="_blank"></a>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        visible: false,
        form: {
          name: '',
          region: '',
          date1: '',
          date2: '',
          delivery: false,
          type: [],
          resource: '',
          desc: ''
        }
      }
    },
    methods: {
      onSubmit() {
        console.log('submit!');
      }
    }
  }
</script>

<style lang="stylus" scoped rel='stylesheet/stylus'>
  #portfolio
    width 700px
    margin 40px auto
</style>



